<!-- eslint-disable vue/no-v-html -->
<template>
  <div>
    <div class="joiner is-hidden-tablet" />

    <div>
      <div class="narrow-block has-text-light">
        <div class="columns">
          <div class="column is-narrow has-text-right has-text-centered-mobile">
            <h2 class="fancy-text is-size-2 is-size-3-mobile fade-in-down delay-point-25">
              interact.js Pro:
            </h2>
          </div>
          <div class="column">
            <h2
              ref="desc"
              :aria-label="fullDesc"
              class="fancy-text is-size-2 is-size-3-mobile is-spaced fade-in-down delay-point-25"
            >
              <div>
                <span class="is-hidden-mobile">the</span>
                <span class="text-carousel__stage">
                  <span
                    v-for="(desc, index) of descriptions"
                    :key="index"
                    class="text-carousel__item"
                    style="position: absolute; top: 0"
                  >
                    <transition name="slide-up">
                      <span
                        v-if="index === descIndex"
                        class="is-inline-block"
                        v-html="getDescriptionHtml(desc)"
                      />
                    </transition>
                  </span>
                </span>
              </div>
              <div class="has-text-centered-mobile">
                JavaScript drag and drop library on the Web
              </div>
            </h2>

            <div class="spacer is-double" />

            <p class="is-size-5 fade-in delay-point-50 has-text-centered-mobile">
              Pro builds on the open source features of interact.js, adding even
              more methods and helpers that help you build super smooth
              interactions that look and feel great on desktop and mobile
              browsers.
            </p>

            <div class="spacer is-double" />

            <div class="level has-text-centered-mobile fade-in delay-point-75">
              <div class="level-left">
                <div
                  v-if="false"
                  class="level-item"
                >
                  <IntExtLink
                    v-if="1"
                    to="/pro/checkout"
                    class="button is-medium is-danger has-text-weight-bold is-spaced-right-tablet"
                  >
                    Get .js Pro for $99<sup>/yr</sup>
                  </IntExtLink>
                </div>
                <div class="level-item">
                  <IntExtLink
                    class="button is-medium is-outlined is-inverted is-primary"
                    to="#more-info"
                  >
                    Learn more
                  </IntExtLink>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'

import { whenScrolledInView } from '../../utils'

export default Vue.extend({
  metaInfo () {
    return {
      description: this.fullDesc
    }
  },
  data () {
    return {
      descriptions: [
        'smoothest',
        'snappiest',
        'springiest',
        'stretchiest',
        'simplest',
        // 'slickest',
        'most customizable'
      ],
      descTimeout: 0,
      descIndex: 0
    }
  },
  computed: {
    fullDesc () { return `the ${this.descriptions.join(', ')} Javascript drag and drop library on the Web` }
  },
  mounted () {
    this.animateDesc()
  },
  methods: {
    animateDesc () {
      this.descTimeout = setTimeout(async () => {
        await whenScrolledInView(this.$refs.desc)

        this.descIndex = (this.descIndex + 1) % this.descriptions.length

        this.animateDesc()
      }, 3000)
    },
    getDescriptionHtml (desc) {
      return `<span class="is-hidden-tablet">the</span>&nbsp;&nbsp;${desc.replace(/ /g, '&nbsp;')}`
    }
  }
})
</script>
